@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300;400;700&family=Noto+Sans+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0}body{background:#31373D;overflow:hidden;font-family:'Heebo', sans-serif;font-size:14px}.container{width:768px;margin:0 auto}.lds-ring{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:inline-block;width:40px;height:40px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #fff;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#fff transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.weather-manager{display:flex;width:100%;padding:30px 0;margin-top:30px;justify-content:space-around;background:white;border-radius:15px}.weather-manager .city-search{display:flex}.weather-manager .city-search__submit{border-radius:15px;background:#72db72;border:none;color:#31373D;padding:10px 15px;margin-left:10px}.weather-card{border-radius:15px;padding:25px;margin-top:60px;background:white}.weather-card__title{color:#31373D;font-weight:bold;font-size:1.2em}.weather-main{display:flex;flex-direction:column}.weather__wrapper{display:flex;align-items:center}.weather__city{margin-top:15px;font-weight:bold;color:#31373D;font-size:1.3em}.weather__type{font-weight:bold;font-size:1.1em;color:#31373D}.weather__feels-like{margin-top:-20px !important;margin-bottom:20px !important;font-size:1.2em}.weather__temp{display:flex;align-items:center}.weather__temp img{width:120px;height:120px}.weather__temp span{font-size:4em;color:#31373D}.weather-detail{display:flex;flex-direction:column;text-transform:uppercase;font-weight:bold;font-size:1.1em;color:#31373D;margin-left:150px}.weather-detail span{margin:5px 0}.weather-detail i{margin-right:5px}.no-weather{font-size:3em;margin-top:30px;color:#31373D;font-weight:bold;text-transform:uppercase;text-align:center}

